.show-section {
  padding: 30px 30px 50px 43px;
}

.showroom-slide {
  padding: 0 50px;
  margin-top: 46px;
  position: relative;
}

.showroom-slide .item {
  height: 100%;
}

.showroom-slide .item a {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #707070;
  position: relative;
}

.showroom-slide .item a:before {
  content: "";
  width: 0;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: all .3s;
}

.showroom-slide .item a:hover:before {
  opacity: 1;
  width: 100%;
}

.showroom-slide .item a:hover .r-zoom {
  opacity: 1;
  transform: translate(-50%, -50%) rotate(90deg);
}

.showroom-slide .item a .r-zoom {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all .3s;
}

.showroom-slide .item a .r-zoom i {
  color: #fff;
  font-size: 15px;
}

.showroom-slide .owl-nav button {
  position: absolute;
  top: 50%;
  transition: all .3s;
  transform: translateY(-50%);
}

.showroom-slide .owl-nav button.owl-prev {
  left: 10px;
}

.showroom-slide .owl-nav button.owl-next {
  right: 10px;
}

.showroom-slide .owl-nav button span {
  display: none;
}

.showroom-slide .owl-nav button .fas {
  font-size: 30px;
  color: #000000;
  transition: all .3s;
}

.showroom-slide .owl-nav button:focus {
  outline: 0;
}

.showroom-slide .owl-nav button:hover .fas {
  color: #000000;
}

@media (max-width: 1240px) {
  .showroom-slide .owl-nav button.owl-prev {
    left: 23px;
  }
  .showroom-slide .owl-nav button.owl-next {
    right: 23px;
  }
}

@media (max-width: 767px) {
  .showroom-slide {
    margin-top: 30px;
  }
}

